.content-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.content-wrapper .spellform {
  display: grid;
  grid-template-columns: 1fr;
  position: relative;
}
.content-wrapper .spellform .background {
  height: 1000px;
}
.content-wrapper .spellform .background img {
  height: 1000px;
}
.content-wrapper .spellform .foreground {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 10;
  margin-top: 350px;
  margin-left: 480px;
  font-size: 50px;
}
.content-wrapper .spellform .foreground .live {
  visibility: visible;
}
.content-wrapper .spellform .foreground .dead {
  visibility: hidden;
}
.content-wrapper .hud {
  display: flex;
  justify-content: center;
}
.content-wrapper .hud .mana {
  display: grid;
  height: 200px;
  width: 190px;
}
.content-wrapper .hud .mana img {
  height: 200px;
  width: 190px;
}
.content-wrapper .hud .form {
  display: grid;
  height: 200px;
  width: 190px;
}
.content-wrapper .hud .form img {
  height: 200px;
  width: 190px;
}