html {
  font-size: 62.5%;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  font-family: "montserrat";
}

body {
  overflow-x: hidden;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  -webkit-tap-highlight-color: transparent;
  background-color: black;
}
body .app-wrapper .add-spell {
  display: grid;
  justify-content: center;
  justify-items: center;
}
body .app-wrapper .add-spell img {
  height: 300px;
  width: 100%;
}
body .app-wrapper .add-spell h1 {
  color: white;
}
body .app-wrapper .add-spell .add-spell-page .add-spell-form input {
  font-size: 35px;
}
.app {
  margin-top: 2rem;
  display: grid;
  justify-items: center;
  color: white;
  font-size: 2.5rem;
}

.mana-icon {
  height: 200px;
}